.Main-container {
  background-color: #406cc5;
  z-index: 1;
  min-height: 100vh;
  min-width: 100vw;
  position: relative;
  display: flex;
  flex-direction: row;
}

.Unity-loading {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  /* We'll set the following Flex properties in order to center the text. */
  display: flex;
  justify-content: center;
  align-items: center;
}

.Unity-container {
  background-color: #2a9639;
  flex-direction: column;
  position: relative;
  width: 100%;
  height: 100vh;
}

.Unity-game.blur {
  filter: blur(4px);
  transition: filter 1s;
}

.Unity-game {
  height: 100%;
  width: 100%;
  background-color: blue;
  transition: filter 1s;
}

.Toggle {
  background-color: #164624;
  position: absolute;
}

.Info-container {
  background-color: #92193e;
  flex-direction: column;
  position: absolute;
  overflow-y: scroll;
  float: right;

  font-family: helvetica;

  z-index: 4;
  top: 0;
  right: 0;
  height: 100%;
  width: 25%;
  transform: translate3d(50vw, 0, 0);
  transition: transform 0.5s;
}

.Info-container.active {
  transform: translate3d(0, 0, 0);
  transition: transform 0.5s;
}

.Info-object {
  margin: 5px;
  padding: 5px;;
  background-color: beige;
  word-wrap: break-word;
}

.Info-header {
  background-color: #164624;
}


.Info-main {
  background-color: #4b9661c5;
}


.Info-footer {
  display: flex;
  background-color: #2bce5c;
  justify-content: flex-end;
}

.Flag {
  display: flex;
  text-align: center;
  align-items: center;
  background-color: brown;
  text-align: left;
  flex: 1;
}

.Icon {
  display: flex;
  text-align: center;
  justify-content: center;
  align-items: center;
  border: 1px solid black;
  border-radius: 1px;
  background-color: aquamarine;

  font-size: x-large;
  color: black;

  margin: 1px;

  height: 50px;
  width: 50px;
}

.Icon:hover {
  transform: scale(1.1);
  filter: brightness(95%);
}

.Info-backround {
  background-color: rgb(0, 0, 0, 0.5);
  flex-direction: column;
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;

  visibility: hidden;
}

.Info-backround.active {
  visibility: visible;
}

.Info-popup {
  flex-direction: column;
  position: absolute;
  
  display: flex;
  align-items: center;
  justify-content: center;

  top: 0;
  left: 0;
  height: 100%;
  width: 75%;;

  align-content: center;
}

.Info-image {
  background-color: coral;

  max-width: 80%;

  top: 0;
  left: 0;
  bottom: 0;
  right: 0;

  margin: auto;
  padding: 5px;

  transform: scale(0.5);
  transition: transform 0.5s;
}

.Info-image.show {
  transform: scale(1);
  transition: transform 0.5s;
}

.Info-image img{
  width: 100%;
  display: block;
}

.clearing {
  clear: both;
}

.Image-close {
  background-color: brown;
  position: absolute;

  width: 50px;
  height: 50px;
  top: 0;
  right: 0;
  
  float: right;
}